.profile__edit-button {
	padding: 0;
	margin: 16px 0 0;
	width: 24px;
	height: 24px;
	background-color: transparent;
	background-image: url("../../../images/edit-button.svg");
	background-position: center;
	background-repeat: no-repeat;
	border: 1px solid #FFF;
}

@media screen and (max-width: 666px) {
	.profile__edit-button {
		margin: 9px 0 0;
		width: 18px;
		height: 18px;
	}
}